import React from 'react';
import './App.css';
import Primario from './components/primario';

function App() {
    const categoria = ['Youtube', 'Instagram', 'NFT Marketplace'];
    const subcategorias = [
        [
            {
                name : 'Paint With Siddhartha',
                url : 'https://www.youtube.com/PaintWithSiddhartha'
            },
        ],
        [
            {
                name : 'Paint With Siddhartha',
                url : 'https://www.instagram.com/paint_with_siddhartha/'
            },
            {
                name : 'NFT page- Siddha_art',
                url : 'https://www.instagram.com/siddha_art/'
            },

        ],
        [
            {
                name : 'Wazirx Marketplace',
                url : 'https://nft.wazirx.org/siddhartha/creations'
            }
        ]
    ];

    return(
        <div className='Wrapper'>
            <div className='items'>
                <div className='items-head'>
                    <p>Linktree Dashboard</p>
                    <hr/>
                </div>
                {categoria.map( (x, y) => {
                    return <Primario Cat={x} Sub={subcategorias[y]} key={x} />;
                })}
            </div>
        </div>
    );
}

export default App;